@font-face {
  font-family: 'fontello';
  src: url('../icons/fontello/fontello.eot?68112527');
  src: url('../icons/fontello/fontello.eot?68112527#iefix') format('embedded-opentype'),
       url('../icons/fontello/fontello.woff2?68112527') format('woff2'),
       url('../icons/fontello//fontello.woff?68112527') format('woff'),
       url('../icons/fontello/fontello.ttf?68112527') format('truetype'),
       url('../icons/fontello/fontello.svg?68112527#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
